body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.search-form button:hover {
  background-color: #0056b3;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  background: #f8f8f8;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.info-item {
  margin: 0;
}

.load-more {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  width: 200px;
  margin: 20px auto;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.load-more.hidden {
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
}
